body,html{height: 100%;background: #E2E2E2}
body,ul{margin:0;padding:0}
body{font:14px "\5FAE\8F6F\96C5\9ED1","\5B8B\4F53","Arial Narrow",HELVETICA;-webkit-text-size-adjust:100%;}
li{list-style:none}
a{text-decoration:none;}
/*  */
/* nav */
.nav{width: 220px;height: 100%;background: #263238;-webkit-transition: all .3s;transition: all .3s; position: absolute;top:0;left: 0;}
.nav a{display: block;overflow: hidden;padding-left: 0px;line-height: 46px;max-height: 46px;color: #ABB1B7;-webkit-transition: all .3s;transition: all .3s;}
.nav a span{margin-left: 30px;}
.nav-item{position: relative;}
.nav-item.nav-show{border-bottom: none;}
.nav-item ul{display: none;background: rgba(0,0,0,.1);}
.nav-item.nav-show ul{display: block;}
.nav-item>a:before{content: "";position: absolute;left: 0px;width: 2px;height: 46px;background: #34A0CE;opacity:0;-webkit-transition: all .3s;transition: all .3s;}
.nav .nav-icon{font-size: 20px;position: absolute;margin-left:-1px;}
/*  iconfont */
.icon_1::after{content: "\E62B";}
.icon_2::after{content: "\E669";}
.icon_3::after{content: "\E61D";}
/*---------------------*/
.nav-more{float:right;margin-right: 20px;font-size: 12px;-webkit-transition: -webkit-transform .3s;transition: -webkit-transform .3s;transition: transform .3s;transition: transform .3s, -webkit-transform .3s;}
/*  iconfont */
.nav-more::after{content: "\E621";}
/*---------------------*/
.nav-show .nav-more{-webkit-transform:rotate(90deg);transform:rotate(90deg);}
.nav-show,.nav-item>a:hover{color: #FFF;background:rgba(0,0,0,.1);}
.nav-show>a:before,.nav-item>a:hover:before{opacity:1;}
.nav-item li:hover a{color: #FFF;background: rgba(0, 0, 0,.1);}
/* nav-mini */
.nav-mini.nav{width: 60px;}
.nav-mini.nav .nav-icon{/* margin-left:-2px; */}
.nav-mini.nav .nav-item>a span{display: none;}
.nav-mini.nav .nav-more{margin-right: -20px;}
.nav-mini.nav .nav-item ul{position: absolute;top:0px;left:60px;width: 180px;z-index: 99;background:#3C474C;overflow: hidden;}
.nav-mini.nav .nav-item:hover{background:rgba(255,255,255,.1);}
.nav-mini.nav .nav-item:hover .nav-item a{color:#FFF;}
.nav-mini.nav .nav-item:hover a:before{opacity:1;}
.nav-mini.nav .nav-item:hover ul{display: block;}





