.chart *, .chart{-webkit-box-sizing:border-box;box-sizing:border-box;margin:0;padding:0;}
.chart{
  width:auto;
  display:block;
  position:relative;
  font-family:sans-serif;
  font-size:14px;
  margin: 0 auto;
}
/* bar chart */
.chart.bar{height:200px;}
.bar-chart{
  display: table;
  table-layout: fixed;
  width: auto;
  height: 100%;
  margin: 0 auto;
  z-index:20;
  position:absolute;
  left:0px;
}
.bar-chart ul{
  margin-left: 34px;
  float: left;
  display: block;
  height:100%;
}
.bar-chart li{
  position: relative;
  display: table-cell;
  vertical-align: bottom;
  height: 100px;
}
.bar-chart span{
  margin: 0 6px;
  display: block;
  background: rgb(241,127,73);
  width: 34px;
  position:relative;

  -webkit-animation: drawBar 0.3s ease-in-out;
  animation: drawBar 0.3s ease-in-out;

  -webkit-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
}
.bar-chart li:hover span{
  -webkit-box-shadow:0 0 0 4px rgb(241,127,73);
          box-shadow:0 0 0 4px rgb(241,127,73);
  cursor:pointer;
}
.bar-chart li:nth-child(2n+1):hover span{
  -webkit-box-shadow:0 0 0 4px #bd380f;
          box-shadow:0 0 0 4px #bd380f;
}
.bar-chart li:nth-child(2n+1) span{background-color:#bd380f;}
.bar-chart ul:last-of-type li:last-child span{margin-right:0px;}
.bar-chart ul:first-of-type li:first-child span{margin-left:0px;}
.bar-chart ul:last-of-type {margin-right: 0px;}
.bar-chart ul:first-of-type {margin-left: 0px;}
.bar-chart.left-bar ul:last-of-type {margin-right: 0px;}
.bar-chart.left-bar ul{margin-right: 34px;float:left;margin-left:0px;}
.bar-chart span:before {
  position: absolute;
  bottom: 100%;
  margin-bottom: 10px;
  display: block;
  text-align: center;
  content: attr(title);
  word-wrap: break-word;
  font-size: 12px;
  width: auto;

  left: 50%;

  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}
.bar-chart li span:before {
  -webkit-transition: all 100ms ease-in-out;
  transition: all 100ms ease-in-out;
}
@-webkit-keyframes drawBar {0% {height: 0;}}
@keyframes drawBar {0% {height: 0;}}
/* donut chart */
.chart.donut{
  width:200px;
  height:200px;
}
.donut-chart{
  width:100%;height:100%;
  position:relative;
  border-radius:50%;
}
.donut-chart.fill{
  background:rgb(241,127,73);
}
.donut-chart:after{
  content:"";
  position: absolute;
  width: 86%;
  height: 86%;
  background: #FFF;
  left: 50%;
  top: 50%;
  z-index: 400;
  margin: 0 auto;
  pointer-events: none;
  border-radius: 100%;

  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}
.donut-chart h2{
  text-align:center;
  position: absolute;
  line-height: 140%;
  width: 100%;
  margin-top:-30px;
  top:50%;
  z-index:500;
  pointer-events: none;

  font-size:26px;
  font-weight:400;
  color:rgb(0,0,0);
}
.donut-chart h2 span{
  display:block;
  width:100%;
  font-size:14px;
  color:rgb(142,143,143);
  margin-top:-10px;
}
.donut-chart .spinner{
  z-index: 200;
  border-right: none;
  border-radius: 100% 0 0 100% / 50% 0 0 50%;

  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);

  -webkit-animation: rotateDonut 0.3s ease-in-out;
  animation: rotateDonut 0.3s ease-in-out;

  -webkit-transition: all 160ms ease-in-out;
  transition: all 160ms ease-in-out;
}
.spinner ~ .pie{
  pointer-events: none;
}
.donut-chart.fill .pie,
.pie-chart .pie{
  -webkit-box-shadow: 0 0 0 4px #bd380f;
  box-shadow: 0 0 0 4px #bd380f;
}
@-webkit-keyframes rotateDonut {0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}}
@keyframes rotateDonut {0% {-webkit-transform: rotate(0deg);transform: rotate(0deg);}}
.donut-chart .spinner.temp{display:none;}
.donut-chart .pie{
  width: 50%;
  height: 100%;
  position: absolute;
  background: #bd380f;

  -webkit-transform-origin: 100% 50%;
  transform-origin: 100% 50%;
}
.donut-chart .mask{
  background:rgb(248,248,248);
  z-index: 400;
  margin-left: -1px;
}
.pie-chart:after{
  display:none;
}
.chart.donut .pie-chart h2,
.chart.donut .pie-chart h2 span{color:#FFF;}
/* pie chart */
.chart.pie svg{width:100%;height:100%;}
.chart.pie {
  width: 200px;
  height: 200px;
}
.chart.pie svg text{
  text-anchor: middle;
  fill:white;
  stroke-width:0;
  -webkit-transform:translate(120,-80);
          transform:translate(120,-80);
}
.chart.pie svg path:hover{
  stroke-width:10px;
}
.charts-tip{
  display:block;
