}
.chart.pie .pie-legend{
  position:absolute;
  top:50%;
  right:0;
  margin-right:-30px;
  -webkit-transform:translateX(100%) translateY(-50%);
          transform:translateX(100%) translateY(-50%);
}
.chart.pie .pie-legend li{
  display:block;
  margin-bottom:14px;
  clear:both;
}
.chart.pie .pie-legend li i{
  top:1px;
