/* line chart */
.chart.line{width:auto;height:auto;margin:0 auto;position:relative;}
.chart .line-chart{
  min-width:100px;
  min-height:100px;
  width:100%;
  height:100%;
  margin:0px auto;
  display:block;
  position:relative;
