/* line chart points tooltip */
.line-chart li a:hover:before,
.line-chart li a:hover:after{display:block;z-index:10;}
.line-chart li a:before{
  content:attr(data-y);
  cursor:pointer;
  display:none;
  position:absolute;
  font-size:14px;
  color:#FFF;
  bottom:100%;
  left:50%;
  margin-bottom:-4px;
