.chart.line svg,
.chart.line > div{
  height: 100%;
  width: 100%;
  position:absolute;
  bottom:0px;
  left:0px;
  opacity:1;

  -webkit-animation: drawLine .3s ease-in-out;
    animation: drawLine .3s ease-in-out;
}
.chart.line svg path{
  fill: rgba(241,127,73,0.0);
  stroke: rgb(241,127,73);
  stroke-width: 2px;
}
