.ol-control-ctv .tooltip .tooltip-text::after {
    content: "";
    position: absolute;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
}
/*  */
.ol-control-ctv.ol-control-ctv-horizontal .tooltip .tooltip-text.tooltip-bottom {
    top: 120%;
    left: 50%;
    margin-left: -60px;
}
.ol-control-ctv.ol-control-ctv-horizontal .tooltip .tooltip-text.tooltip-bottom::after {
    bottom: 100%;
    left: 50%;
    border-color: transparent transparent black transparent;
}
/*  */
.ol-control-ctv.ol-control-ctv-horizontal .tooltip .tooltip-text.tooltip-top {
    bottom: 120%;
    left: 50%;
    margin-left: -60px;
}
.ol-control-ctv.ol-control-ctv-horizontal .tooltip .tooltip-text.tooltip-top::after {
    top: 100%;
    left: 50%;
    border-color: black transparent transparent transparent;
}
/*  */
.ol-control-ctv.ol-control-ctv-vertical .tooltip .tooltip-text.tooltip-right {
    top: 50%;
    left: 120%;
}
.ol-control-ctv.ol-control-ctv-vertical .tooltip .tooltip-text.tooltip-right::after {
    top: 33%;
    right: 100%;
    border-color: transparent black transparent transparent;
}
/*  */
.ol-control-ctv.ol-control-ctv-vertical .tooltip .tooltip-text.tooltip-left {
    top: 50%;
    right: 110%;
}
.ol-control-ctv.ol-control-ctv-vertical .tooltip .tooltip-text.tooltip-left::after {
    top: 33%;
    left: 104%;
    border-color: transparent transparent transparent black;
}
.ol-control-ctv.ol-control-ctv-horizontal .tooltip:hover .tooltip-text,
.ol-control-ctv.ol-control-ctv-vertical .tooltip:hover .tooltip-text {
    display: block;
}